.launchShow {
  margin: 30px;
  border: solid;
  padding: 30px;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-column-gap: 20px;
  grid-row-gap: 50px;
}

.launchDetail {
  text-align: center;
  grid-column: 1 / 3;
}

.launchComments {
  text-align: center;
  
}



